@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
* {
  box-sizing: border-box;
}

:root {
  --nav-height: 60px;
  --margin-between-items: 20px;
  --blue-color: #00acdc;
  --underline-width: 36px;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.logo {
  flex-shrink: 0;
  width: 40px;
  display: flex;
}

.header {
  height: var(--nav-height);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  padding: 23px 0;
  line-height: normal;
  text-decoration: none;
  display: block;
}

.nav__item:not(:last-child) {
  margin-right: var(--margin-between-items);
}

.nav__list {
  text-align: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link:hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  width: var(--underline-width);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

